import styled from 'styled-components'
import { colors, layout } from '../../const'

export const Landing = styled.section`
  height: 100vh;
  background: linear-gradient(105deg, ${colors.primary} 0%, ${colors.secondary} 50%);
  clip-path: polygon(0% 20%, 20% 0%, 100% 0%, 100% 80%, 80% 100%, 0% 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
`

export const Link = styled.a`
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 999;

  img {
    display: block;
    width: 65px;
    height: 65px;
    transition: 0.5s all;

    &:hover {
      transform: rotate(360deg);
    }
  }
`

export const Title = styled.h1`
  color: ${colors.white};
  font-size: 64px;
  letter-spacing: 2px;
  margin-bottom: 20px;
  border-bottom: 2px solid white;
  padding-bottom: 10px;
`

export const Subtitle = styled.p`
  color: ${colors.accent};
  font-size: 18px;
  margin-bottom: 70px;
`

export const FeatureTitle = styled.h2`
  font-size: 28px;
  margin-bottom: 25px;
  color: ${colors.white};
  font-weight: normal;
`

export const FeatureContainer = styled.div``
export const Feature = styled.p`
  color: ${colors.white};
  margin-bottom: 10px;
  text-align: center;
`
