import styled from 'styled-components'
import { Input } from '@rocketseat/unform'
import { colors, layout } from '../../const'

export const StyledForm = styled.div`
  h1 {
    text-align: center;
    margin-bottom: 25px;
    color: ${colors.grayDark};
    font-size: 24px;
  }

  & {
    width: 100%;
    margin: 0 auto;
    display: block;
    background-color: ${colors.white};
    box-shadow: 0px 3px 15px 0px rgba(${colors.shadow}, 0.2);
    border-radius: 20px;
    padding: 25px;
  }

  label {
    display: block;
    margin-bottom: 5px;
    font-size: 13px;
    font-weight: bold;
    color: ${colors.grayDark};
  }

  button[type='submit'] {
    border-radius: ${layout.borderRadius};
    display: block;
    width: 50%;
    background-color: ${colors.primary};
    color: ${colors.white};
    border: none;
    outline: none;
    text-align: center;
    font-size: 18px;
    cursor: pointer;
    padding: 15px;
    transition: 0.3s all;
    margin: 0 auto;
    margin-top: 40px;

    &:hover {
      transform: scale(1.05);
    }
  }
`

export const StyledInput = styled(Input)`
  display: block;
  width: 100%;
  border-radius: ${layout.borderRadius};
  outline: none;
  font-size: 16px;
  font-family: 'Lato', sans-serif;
  padding: 8px 15px;
  border: 1px solid ${colors.accent};

  &:not(:last-child) {
    margin-bottom: 20px;
  }
`
