import React, { useState } from 'react'
import Section from '../Section'
import { Form, Scope } from '@rocketseat/unform'
import CodeBlock from '../CodeBlock'
import { StyledForm, StyledInput } from '../Form'
import { Wrapper, Content, TopWrapper, FormTitle, ButtonContainer, Button } from './styles'

export default () => {
  const initialFormCode = `
  import React from "react"
  import { Form, Input, Scope } from "@rocketseat/unform"
  
  function App() {
    function handleSubmit(data) {
      console.log(data)
    }
  
    return (
      <Form onSubmit={handleSubmit}>
        <Scope path="user">
          <Input label="Name" name="name" />
          <Input label="Email" name="email" />
          <Input label="Password" name="password" type="password" />
        </Scope>
        <Scope path="address">
          <Input label="Street" name="street" />
          <Input label="Number" name="number" />
        </Scope>
        <button type="submit">Register</button>
      </Form>
    )
  }
        `

  const initialWindowTitle = 'UnformDemo.jsx'

  const [windowContent, setWindowContent] = useState(initialFormCode)

  const [formData, setFormData] = useState('')
  const [windowTitle, setWindowTitle] = useState(initialWindowTitle)

  const handleSubmit = data => {
    setFormData(JSON.stringify(data, null, 2))
    setWindowContent(JSON.stringify(data, null, 2))
    setWindowTitle('unformDemo.json')
  }

  return (
    <Section>
      <TopWrapper>
        <FormTitle>Try the form, it's dead simple!</FormTitle>
        <ButtonContainer>
          <Button
            onClick={() => {
              setWindowContent(initialFormCode)
              setWindowTitle('UnformDemo.jsx')
            }}
          >
            Code
          </Button>
          <Button
            onClick={() => {
              setWindowContent(formData)
              setWindowTitle('unformDemo.json')
            }}
          >
            Output
          </Button>
        </ButtonContainer>
      </TopWrapper>
      <Wrapper>
        <Content>
          <StyledForm>
            <Form onSubmit={handleSubmit} customStyle={{ backgroundColor: 'black' }}>
              <h1>Hey, welcome!</h1>
              <Scope path="user">
                <StyledInput label="Name" name="name" />
                <StyledInput label="Email" name="email" />
                <StyledInput label="Password" name="password" type="password" />
              </Scope>
              <Scope path="address">
                <StyledInput label="Street" name="street" />
                <StyledInput label="Number" name="number" />
              </Scope>
              <button type="submit">Register</button>
            </Form>
          </StyledForm>
        </Content>

        <Content>
          <CodeBlock
            showLineNumbers
            title={windowTitle}
            customStyle={{ borderRadius: 10 }}
            code={windowContent}
          />
        </Content>
      </Wrapper>
    </Section>
  )
}
